<template>
  <div class="video_tabel">
    <div class="top">
      <div class="start">
        <h1 class="title">
          <i :class="'red iconfont '+data.iconClass"></i>
          <span>{{$t(data.title)}}</span>
        </h1>
        <div class="attach">
          <span class="attach_str">{{$t(data.attach)}}</span>
          <i :class="'attach_icon iconfont '+data.attachIcon"></i>
        </div>
      </div>
      <div class="end">
        <i class="red updata_icon iconfont icongengxin"></i>
        <span class="updata_str">{{$t("updata")}}</span>
      </div>
    </div>
    <ul class="list" v-if="data.list">
      <li v-for="d in data.list.data" :key="d.id" class="item" @click="itemClick(d.href)">
        <div class="bg" :style="'background: url('+d.bg+') center center/cover no-repeat'">
          <span class="bg_top">
            <span class="bg_year">{{d.year}}</span>
            <span class="bg_type">{{d.type}}</span>
          </span>
          <span class="bg_text">{{d.pic_text}}</span>
        </div>
        <div class="content">
          <h2 class="title">{{d.title}}</h2>
          <p class="to_star">{{d.to_star.substr(0,10)}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  props: {
    data: {
      iconClass: String, //标题图标类名
      title: String, //标题，要求i18n里面的key
      attach: String, //附加信息文字，要求i18n里面的key
      attachIcon: String, //附加信息图标类名
      list: Array //数据Json
    }
  },
  mounted() {},
  methods: {
    itemClick(url) {
      this.$store.commit("changeSwitchPage", "Details")
    }
  }
};
</script>

<style lang="less" scoped>
.red {
  color: red;
}
.video_tabel {
  margin-top: 20px;
}
.top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  .start {
    display: flex;
    align-items: center;
  }
  .title {
    font-size: 1.25em;
    i {
      margin-right: 5px;
    }
  }
  .attach {
    margin-left: 20px;
    font-size: 13px;
  }
  .attach_icon {
    font-size: 13px;
  }
  .updata_icon {
    font-size: 14px;
  }
  .updata_str {
    margin-left: 5px;
    font-size: 14px;
  }
}
.list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin-top: 20px;
}
.item {
  width: 118px;
  display: flex;
  align-content: flex-start;
  flex-wrap: wrap;
  margin-bottom: 20px;
  background-color: white;
  padding-bottom: 10px;
  border-radius: 0 0 25px;
  .bg {
    width: 98%;
    height: 172px;
    position: relative;
    span {
      font-size: 11px;
      padding: 2px 5px;
      color: white;
    }
  }
  .bg_top {
    position: absolute;
    top: 5px;
    left: 0;
    span {
      border-radius: 5px;
    }
    .bg_year {
      background-color: orangered;
      margin-right: 5px;
    }
    .bg_type {
      background-color: orange;
    }
  }
  .bg_text {
    position: absolute;
    bottom: 0;
    right: 0;
    background-color: black;
  }
  .content {
    padding: 8px;
    box-sizing: border-box;
    flex: 1;
    height: 50px;
    overflow: hidden;
    .title {
      font-size: 1em;
      font-weight: 550;
    }
  }
  .to_star {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
    font-size: 14px;
  }
}
</style>